import React from "react"
import styled from 'styled-components'
import { graphql, useStaticQuery } from 'gatsby'
import Img from 'gatsby-image'

const Header = ({ title, height, CustomSideBar }) => {
  const data = useStaticQuery(graphql`
    query {
      file(relativePath: {eq: "header-image.jpg"}) {
        id
        childImageSharp {
          fluid (
            maxWidth: 1440
          ) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <StyledHeader height={height}>
      <Img
        className="image"
        fluid={data.file.childImageSharp.fluid}
        alt={title}
        style={{ position: 'absolute' }}
      />
      <div className='compress'>
        <div className='sidebar'>
          {CustomSideBar
            ? <CustomSideBar />
            : <h1 className='title'>{title}</h1>
          }
        </div>
      </div>
    </StyledHeader>
  )
}

const StyledHeader = styled.header`
  height: ${p => p.height ? p.height : '18.75rem'};
  position: relative;

  .image {
    height: 100%;
    width: 100%;
    z-index: -1;
  }

  .compress {
    display: flex;
    justify-content: flex-end;
    height: 100%;

    @media (max-width: ${p => p.theme.media.large}px) {
      justify-content: center;
    } 

    .sidebar {
      width: ${p => p.theme.sidebar.width};
      background-color: ${p => p.theme.colors.whiteOverlay};

      padding: 2.5rem 2rem 0 2rem;

      display: flex;
      justify-content: center;

      h1.title {
        align-self: center;

        font-size: 1.5625rem;
        font-weight: 700;
        text-align: center;
      }
    }
  }

`

export default Header
